.registration_block {
  margin-top: 50px;
}
.registration_wrapper {
  margin-bottom: 30px;
}
.registration_text-input {
  font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 12px;
line-height: 15px;
color: #6B6B6B;
margin-top: 3px;
}
.registration_text-bottom{
  font-family: 'Montserrat';
font-style: normal;
font-weight: 600;
font-size: 14px;
line-height: 17px;
color: #959595;
}
.registration_button_block,
.registration_button_block_change {
  display: flex;
  justify-content: space-between;
    align-items: center;
    margin-top: 60px;
    max-width: 767px;
    width: 100%;
    
}
.registration_button_block_change  {
  margin-bottom: 50px;
}
.registration_button_block {
  margin-bottom: 226px;
}
@media (max-width: 520px) {
  .registration_button_block,
  .registration_button_block_change {
    flex-direction: column-reverse;
  }
  .registration_button-text {
    margin-bottom: 30px;
  }
  .registration_button_block {
    margin-top: 30px;
  }
  .registration_button_block {
    margin-bottom: 72px;
  }
  .registration_text-bottom {
    font-size: 12px;
line-height: 15px;
  }
  .registration_wrapper {
    margin-bottom: 15px;
  }
 }

.registration_button-text {
  position: relative;
}
.registration_button-text::before {
  content: "*";
position: absolute;
color: red;
width: 10px;
height: 10px;
font-size: 20px;
top: 6px;
left: -16px;
}
