// }
.registration_text-green {
  font-family: 'Lora';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  color: #4B8333;
  background: #E6F8DE;
  border-radius: 20px;
  max-width: 562px;
      padding: 20px;
      margin-bottom: 50px;
      
      // display: none;
}
@media (max-width: 470px) {
  .registration_text-green {
    font-size: 10px;
    line-height: 13px;
    padding: 10px;
    max-width: 320px;
    margin-bottom: 20px;
    font-size: 10px;
line-height: 13px;
padding: 10px;
  }
 }

 .registration_sms-text {
  font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 17px;
color: #868686;
margin-top: 48px;
 }
 .registration_sms_wrap {
  display: flex;
  flex-direction: column;
  padding: 20px 0 62px;

 }
 @media (max-width: 570px) {
  .registration_sms_wrap {
    align-items: center;
   }
   .registration_sms-text {
  margin-top: 40px;
   }
 }

 .registration_sms_btn {
  max-width: 170px;
  width: 100%;
 }

 .registration_sms {
  display: none;
 }
 .registration_button_block_change {
  max-width: 635px;
 }