.authorization_wrap {
  max-width: 635px;
  width: 100%;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 50px;
}
@media (max-width: 470px) {
  .authorization_wrap {
    grid-gap: 30px;
  }
 }
 @media (max-width: 420px) {
  .authorization_wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
 }
.authorization_text-bottom:nth-child(3) {
  margin-left: 22px;
  position: relative;
}
.authorization_button:first-child,
.authorization_button:hover,
.authorization_button:active{
  color: #FFFFFF;
  background: #1E1E1E;
 border-radius: 20px;
}
.authorization_button {
  padding: 10px 40px;
// width: 188px;
height: 40px;
background: #FFFFFF;
border: 1px solid #000000;
border-radius: 20px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
cursor: pointer;
display: flex;
justify-content: center;
align-items: center;
