.recovery_line {
  color: #D9D9D9;;
  height: 1px;
  width: 100%;
}

.recovery_bottom_wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 72px;
}
@media (max-width: 520px) {
  .recovery_bottom_wrapper {
    display: none
  }
  }
.recovery_bottom_text {
  font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 17px;
color: #868686;
width: 100%;
    max-width: 193px;
    cursor: pointer;
}

