
      .authorization_item {
        font-family: 'Montserrat';
font-style: normal;
font-weight: 300;
font-size: 16px;
line-height: 20px;
position: relative;
margin-right: 70px;
      }

      .authorization_list {
        display: flex;
        // max-width: 416px;
        // width: 100%;
        // justify-content: space-between;
        
      }

      .authorization_item:not(:last-child)::after
       {
        content: "";
        position: absolute;
        background: url(../images/arrow.svg) no-repeat 50%;
        width: 8px;
        height: 8px;
        top: 6.5px;
        right: -32px;
      }
      .authorization_line {
        width: 100%;
        color: #C8C4C4;
        height: 1px;
        margin-top: 44px;
      }
      @media (max-width: 520px) {
        .authorization_line {
          display: none;
        }
        .authorization_top {
          display: flex;
          flex-direction: column-reverse;
        }
        .authorization_item:not(:last-child)::after
       {
        right: -28px;
      }
      .authorization_item {
        margin-bottom: 23px;
      }
      .authorization_item {
        font-size: 10px;
        margin-right: 40px;
      }
      .authorization_list {
        // max-width: 276px;
        padding-top: 13px;
      }
      .authorization_title {
        margin-bottom: 0px;border-bottom: 1px solid #C8C4C4;
        padding-bottom: 7px;
      }
      }
      